import React from 'react';
import styled from 'styled-components';
import media from 'utils/styles/media';
import Column from './Column';

export default function Card (props) {

    const widthCorner = 10;

    const Container = styled.div`
        overlow: hidden;
        pointer-events: none;
        background-color:white;
        z-index: 2;
        padding: 10px;
        margin-bottom: 10px;
        
        ${media.tablet`
            opacity: .7;
            background-color:#f2f2f2;
            width: 220px;
            ${propsS => propsS.bottom  ? 'bottom:' + propsS.bottom + '%;' : ''}
            ${propsS => propsS.top ? 'top:' + propsS.top + '%;' : ''}
            position:absolute;
            margin: 0;

            ${Column}:hover & {
                background-color:white;
                opacity: 1;

            }
        `}

        h5 {
            margin: 0 0 10px 0;
        }
        
        p {
            margin: 0;
        }
    `

    const Point = styled.div`
        width: ${widthCorner}px;
        height: ${widthCorner}px;
        background-color: rgb(251, 140, 0);
        border-radius: 100%;
        position: absolute;
        top: ${propsS => propsS.bottom ? `calc(100% - ${widthCorner/2}px);` : `-${widthCorner/2}px;`}
        left: -${widthCorner/2}px;
    `;
    return (

        <Container {...props}>
            {props.children}
            <Point bottom={props.bottom} />
        </Container>

    );
}